.hero {
  margin-bottom: 60px;

  .slider {
    position: relative;
    width: 100%;
    &__arrow {
      display: flex;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 85px;
      height: 85px;
      padding: 20px 25px;
      opacity: 0.8;
      background-color: $white;
      cursor: pointer;
      &:hover {
        opacity: 1; }
      &--prev {
        left: 15px; }
      &--next {
        right: 15px; } }

    .slide {
      position: relative;

      &-img {
        img {
          width: 100%;
          height: 500px;
          object-fit: cover; } }

      &-cont {
        position: absolute;
        top: 20%;
        left: calc(50% - 700px);
        @include glb;
        &__top {
          font-size: 48px;
          span {
            color: $red; } }
        &__center {
          font-size: 100px; }
        &__bottom {
          padding: 10px 30px;
          font-size: 36px;
          color: $white;
          background-color: $red; } } } } }
