.promo {
  margin-bottom: 60px;

  &__list {
    display: flex;
    justify-content: space-between; }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center; }

  &__pic {
    display: flex;
    margin-top: auto;
    margin-bottom: 25px; }

  &__title {
    text-align: center;
    width: 210px;
    @include glb;
    font-size: 22px; } }
