.hero .slider {
  position: relative;
  width: 100%;
}

.hero .slider__arrow {
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 85px;
  height: 85px;
  padding: 20px 25px;
  opacity: 0.8;
  background-color: #ffffff;
  cursor: pointer;
}

.hero .slider__arrow:hover {
  opacity: 1;
}

.hero .slider__arrow--prev {
  left: 15px;
}

.hero .slider__arrow--next {
  right: 15px;
}

.hero .slider .slide {
  position: relative;
}

.hero .slider .slide-img img {
  width: 100%;
  height: 500px;
  object-fit: cover;
}

.hero .slider .slide-cont {
  position: absolute;
  top: 20%;
  left: calc(50% - 700px);
  font-family: glb, sans-serif;
  font-size: 30px;
  line-height: 1.25;
  font-weight: 400;
  color: #004f9f;
}

.hero .slider .slide-cont__top {
