$blue-nav: #6d86a0;
$blue-footer: #7293b4;
$dark-blue: #004f9f;
$green: #70a513;
$white: #ffffff;
$black: #000000;
$red: #ce1629;

@include font(glr, 'gilroy-regular');
@include font(glb, 'gilroy-bold');

@mixin glr {
  font-family: glr, sans-serif;
  font-size: 18px;
  line-height: 1.25;
  font-weight: 400;
  color: $black; }

@mixin glb {
  font-family: glb, sans-serif;
  font-size: 30px;
  line-height: 1.25;
  font-weight: 400;
  color: $dark-blue; }

body {
	@include glr; }
a {
  text-decoration: none;
  color: #7293b4; }
