.services {

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px;
    margin-bottom: 40px; }

  &__pic {
    margin-bottom: 30px; }

  &__title {
    @include glb;
    font-size: 24px; }


  &__price {
    display: flex;
    &-money {
      padding: 0;
      padding-right: 15px;
      margin-right: 15px;
      border-right: 1px solid $black; } } }
