.footer {
  background-color: #f6f7fa;

  &__content {
    display: flex;
    justify-content: space-between;

    padding: 40px 0;

    &-list {
      &-item {
        line-height: 1.8;
        a {
          &:hover {
            color: $dark-blue;
            text-decoration: underline; } } } } }

  &__list {
    display: flex;
    &-item {
      width: 330px; }
    &-title {
      margin-bottom: 30px; } }

  &__contacts {
    width: 315px;

    .logo {
      width: 240px;
      height: 100px;
      margin-bottom: 30px;
      img {
        width: 100%;
        height: 100%; } }

    .contacts {
      &__list {
        flex-direction: column;
        flex-flow: wrap; }
      &__item {
        &-bottom {
          flex-direction: column; }
        &-phone {
          font-size: 16px; }
        &-metro {
          color: $green; } }
      &__item:nth-child(3) {
        order: -1; } } }

  &__copiright {
    background-color: #e1e3ed;
    &-cont {
      display: flex; }
    &-item {
      display: flex; } }

  &__floor {
    height: 15px;
    background-color: #f6f7fa; } }
