.contacts {

  &__list {
    display: flex;
    width: 1100px;
    justify-content: space-between; }

  &__item {
    display: block;
    width: 315px;
    padding-left: 50px;
    &:last-child {
      width: 400px; }

    &-bottom {
      display: flex;
      font-size: 16px; }

    &-metro {
      position: relative;
      @include glb;
      color: $dark-blue;
      &::after {
        content: "";
        background: url("../img/metro.png") no-repeat;
        position: absolute;
        top: 5px;
        left: -40px;
        width: 30px;
        height: 30px; } }

    &-phone {
      position: relative;
      @include glb;
      color: $dark-blue;
      &::after {
        content: "";
        background: url("../img/phone-1.png") no-repeat;
        position: absolute;
        top: 5px;
        left: -40px;
        width: 30px;
        height: 30px; } } }

  &__phone-second {
    position: relative;
    &::after {
      content: "";
      background: url("../img/phone-2.png") no-repeat;
      position: absolute;
      top: 5px;
      left: -40px;
      width: 30px;
      height: 30px; } }

  &__mail {
    position: relative;
    &::after {
      content: "";
      background: url("../img/mail.png") no-repeat;
      position: absolute;
      top: 5px;
      left: -40px;
      width: 30px;
      height: 30px; } }

  &__location {
    position: relative;
    font-size: 16px;
    &::after {
      content: "";
      background: url("../img/marker.png") no-repeat;
      position: absolute;
      top: 5px;
      left: -40px;
      width: 30px;
      height: 30px; } } }
