.description {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;

  &__item {
    width: 690px;

    &-title {
      @include glb;
      font-size: 24px;
      margin-bottom: 30px; }

    &-h3 {
      @include glb;
      font-size: 18px;
      margin-bottom: 30px; } }

  &__text {
    margin-bottom: 30px;
    &--bold {
      @include glb;
      font-size: 18px;
      color: $black; }

    &-item {
      position: relative;
      padding-left: 25px;
      &::before {
        position: absolute;
        top: 8px;
        left: 0;
        content: "";
        width: 10px;
        height: 2px;
        background-color: $green; } } }

  &__pic {
    margin-bottom: 30px; } }
